.experience .content {
  max-width: 85%;
  margin: auto;
}

.experience .box {
  padding: 40px;
}

.experience hr {
  margin: 30px 0 30px 0;
  border: 1px solid #e3e9ed;
  background-color: #e3e9ed;
}

.experience .rate .btn_shadow {
  color: #ff014f;
  padding: 15px 30px;
  font-weight: bold;
  transition: 0.5s;
}

.experience p {
  font-size: 18px;
  transition: 0.5s;
}

.experience .box {
  margin-bottom: 40px;
  position: relative;
}

.experience .btn_shadow:hover {
  transform: translate(0);
}

.experience .box:hover .btn_shadow {
  background: #ff3a75;
  box-shadow: none;
  color: #fff;
}

.experience .box:hover p {
  color: #fff;
}

.experience .content-section {
  position: relative;
}

.experience .left {
  position: relative;
}

.experience .left::after {
  content: '';
  position: absolute;
  top: 16%;
  left: 0;
  height: 118vh;
  width: 5px;
  background: #d9dee2;
  z-index: -2;
}

.experience .box::after {
  content: '';
  position: absolute;
  top: 13%;
  left: -8%;
  height: 5px;
  width: 50px;
  background: #d9dee2;
}

.experience .box::before {
  content: '';
  position: absolute;
  top: 11%;
  left: -10%;
  height: 10px;
  width: 10px;
  background: #fff;
  border: 5px solid #d9dee2;
  border-radius: 50%;
  z-index: 5;
  transition: 0.5s;
}

.experience .box:hover.box::before {
  background: #ff3a75;
}

@media (max-width: 768px) {
  .experience .d_flex {
    flex-direction: column;
  }

  .experience .left::after {
    top: 14%;
    height: 105vh;
  }
}
