.post-detail-container {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  --tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1),
    0 4px 6px -4px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color),
    0 4px 6px -4px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  border-radius: 0.5rem;
  padding-bottom: 3rem;
  margin-bottom: 2rem;
}

.post-detail-image-container {
  position: relative;
  overflow: hidden;
  --tw-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color),
    0 2px 4px -2px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  margin-bottom: 1.5rem;
}

.post-detail-image {
  object-position: top;
  height: 100%;
  width: 100%;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

.post-detail-content-container {
  padding-left: 1rem;
  padding-right: 1rem;
}

.post-detail-content-inner {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  width: 100%;
}

.post-detail-content-author-container {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  width: 100%;
  margin-right: 2rem;
}

.post-detail-content-author-image {
  vertical-align: middle;
  border-radius: 9999px;
}

.post-detail-content-author-name {
  display: inline;
  vertical-align: middle;
  --tw-text-opacity: 1;
  color: rgb(55 65 81 / var(--tw-text-opacity));
  margin-left: 0.5rem;
  font-size: 1.125rem;
  line-height: 1.75rem;
  align-items: center;
}

.post-detail-content-date-container {
  font-weight: 500;
  --tw-text-opacity: 1;
  color: rgb(55 65 81 / var(--tw-text-opacity));
  /* vertical-align: middle; */
  /* align-items: center; */
}

.post-detail-content-date-logo {
  height: 1.5rem;
  width: 1.5rem;
  display: inline;
  margin-right: 0.5rem;
  --tw-text-opacity: 1;
  color: rgb(236 72 153 / var(--tw-text-opacity));
  /* align-items: center; */
}

.post-detail-content-title {
  margin-bottom: 2rem;
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 600;
}

@media (min-width: 1024px) {
  .post-detail-container {
    padding: 2rem;
  }

  .post-detail-content-container {
    padding-left: 0px;
    padding-right: 0px;
  }

  .post-detail-content-author-container {
    margin-bottom: 0;
    width: auto;
  }
}
