.blog-post-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  margin-bottom: 0.75rem;
  margin-top: 3rem;
}

.blog-post-grid {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 3rem;
}

.blog-post-detail-container {
  grid-column: span 1 / span 1;
}

.blog-post-aside-container {
  grid-column: span 1 / span 1;
}

.blog-post-aside-inner {
  position: relative;
  top: 2rem;
}

@media (min-width: 640px) {
  .blog-post-container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .blog-post-container {
    max-width: 768px;
  }
}

/* lg */
@media (min-width: 1024px) {
  .blog-post-container {
    max-width: 1024px;
  }

  .blog-post-grid {
    grid-template-columns: repeat(12, minmax(0, 1fr));
  }

  .blog-post-detail-container {
    grid-column: span 8 / span 8;
  }

  .blog-post-aside-container {
    grid-column: span 4 / span 4;
  }

  .blog-post-aside-inner {
    position: -webkit-sticky;
    position: sticky;
  }
}

@media (min-width: 1280px) {
  .blog-post-container {
    max-width: 1280px;
  }
}

@media (min-width: 1536px) {
  .blog-post-container {
    max-width: 1536px;
  }
}
