.testimonials h2 {
  margin: 15px 0 0 0;
}

.testimonials .left {
  width: 35%;
  padding: 35px;
  margin: 20px;
}

.testimonials .right {
  width: 65%;
  padding: 0 0 0 50px;
  margin: 0 20px 0 0;
}

.testimonials .quote i {
  font-size: 150px;
}

.testimonials .content {
  padding: 50px;
}

.testimonials .content h1 {
  font-weight: 500;
}

.testimonials .content h3 {
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 40px;
}

.testimonials .slide_button {
  position: absolute;
  top: 30px;
  right: 20px;
}

.testimonials .slide_button .prev_btn {
  margin-right: 30px;
}

/* Testimonail slide */
.testimonials .slide {
  max-width: 80%;
  margin: auto;
  height: 500px;
  position: relative;
  display: flex;
  overflow: hidden;
}

article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s linear;
}

article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}

article.lastSlide {
  transform: translateX(-100%);
}

article.nextSlide {
  transform: translateX(100%);
}

/*-----Testimonial slide-----------*/
@media (max-width: 768px) {
  article {
    flex-direction: column;
    padding: 20px;
  }
  .testimonials .left {
    width: 100%;
    display: flex;
    margin: 0;
    margin-top: 15%;
  }
  .testimonials .left img {
    width: 190px;
    border-radius: 10px;
    margin-right: 20px;
  }
  .testimonials .right {
    width: 100%;
    margin: 0;
    padding: 0 0 0 0px;
  }
  .testimonials .slide {
    height: 70vh;
    max-width: 90%;
  }
  .testimonials .quote {
    display: none;
  }
}
