.blog-container {
  margin-top: 2.5rem;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  margin-bottom: 2rem;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 3rem;
}

.blog-grid-card-container {
  grid-column: span 1 / span 1;
}

.blog-side-container {
  grid-column: span 1 / span 1;
}

.blog-side-inner {
  position: relative;
  top: 2rem;
}

@media (min-width: 640px) {
  .blog-container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .blog-container {
    max-width: 768px;
  }
}

/* lg */
@media (min-width: 1024px) {
  .blog-container {
    max-width: 1024px;
  }

  .blog-grid {
    grid-template-columns: repeat(12, minmax(0, 1fr));
  }

  .blog-grid-card-container {
    grid-column: span 8 / span 8;
  }

  .blog-side-container {
    grid-column: span 4 / span 4;
  }

  .blog-side-inner {
    position: -webkit-sticky;
    position: sticky;
  }
}

@media (min-width: 1280px) {
  .blog-container {
    max-width: 1280px;
  }
}

@media (min-width: 1536px) {
  .blog-container {
    max-width: 1536px;
  }
}
