.post-widget-container {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  --tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1),
    0 4px 6px -4px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color),
    0 4px 6px -4px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  border-radius: 0.5rem;
  padding: 2rem;
  margin-bottom: 2rem;
}

.post-widget-title {
  font-size: 1.25rem;
  line-height: 1.75rem;
  margin-bottom: 2rem;
  font-weight: 600;
  border-style: solid;
  border-color: #e5e7eb;
  border-width: 0;
  border-bottom-width: 1px;
  padding-bottom: 1rem;
}

.post-widget-card-container {
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 1rem;
}

.post-widget-card-image-container {
  width: 4rem;
  flex: none;
}

.post-widget-card-image {
  vertical-align: middle;
  border-radius: 9999px;
}

.post-widget-details-container {
  flex-grow: 1;
  margin-left: 1rem;
}

.post-widget-details-date {
  --tw-text-opacity: 1;
  color: rgb(107 114 128 / var(--tw-text-opacity));
  font-size: 0.75rem;
  line-height: 1rem;
}
