.hero {
  position: relative;
  /* height: 90vh; */
}

.hero h3 {
  font-weight: 100;
  letter-spacing: 2px;
}

.hero h1 {
  font-size: 60px;
  font-weight: bold;
}

.hero h1 span {
  color: #ff014f;
}

.hero h2 {
  font-size: 50px;
  font-weight: bold;
  color: #ff014f;
}

.hero h2 span {
  color: black;
}

.hero p {
  color: #878e99;
  font-size: 18px;
  line-height: 30px;
  margin-top: 30px;
}

.hero_btn {
  margin-top: 20%;
}

.hero_btn h4 {
  font-weight: 500;
  letter-spacing: 2px;
  color: #43474b;
  font-size: 15px;
  margin: 30px 0 30px 0;
}

.hero_btn button {
  margin-right: 30px;
}

.hero_btn i {
  font-size: 20px;
}

.hero_btn button img {
  width: 20px;
  height: 20px;
}

.hero .left {
  margin-right: 10%;
}

.hero .right {
  width: 40%;
  position: relative;
}

