.hero .Typewriter {
  display: inline-block;
}

.hero .Typewriter .Typewriter__wrapper {
  color: #3c3e41;
  font-size: 60px !important;
  font-weight: bold;
}

.hero .right_img {
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
  border-radius: 10px;
  height: 580px;
  width: 510px;
  margin-top: 20%;
  /* filter: blur(8px);
  -webkit-filter: blur(8px); */
}

.hero .right_img img {
  position: absolute;
  top: -5%;
  right: 40%;
  width: 400px;
  box-shadow: 0 0 8px 8px white inset;
  z-index: 2;
  /* filter: blur(8px);
  -webkit-filter: blur(8px); */
}

@media (max-width: 768px) {
  .hero .right,
  .hero .left {
    width: 100%;
    margin-left: 20px;
  }

  .hero .f_flex {
    flex-direction: column;
    flex-direction: column-reverse;
  }

  .hero .right_img {
    width: 100%;
    height: 54vh;
  }
}

@media (min-width: 1200px) {
  .hero .right_img img {
    left: -20%;
    top: -5%;
  }
}

.demo {
  height: 200vh;
}
